.footer {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.footer__wrapper {
    margin: 0;
    padding: 79px 70px 20px;
}

.footer__title {
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 16px;
    text-align: center;
    color: #A0A0A0;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid #E8E8E8;
}

.footer__date {
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 16px;
    color: #000000;
    padding: 0;
    margin: 0;
}

.footer__info-wrapper {
    display: flex;
    justify-content: space-between;
    margin: 0;
    padding: 0;
}

.footer__links {
    display: flex;
    gap: 20px;
    margin: 0;
    padding: 0;
}

.footer__link {
    text-decoration: none;
    font-style: normal;
    font-weight: 400;
    font-size: 13px;
    line-height: 16px;
    text-align: right;
    color: #000000;
    cursor: pointer;
}

.footer__link:hover {
    opacity: 0.7;
}

@media (min-width: 321px) and (max-width: 768px) {
    .footer__wrapper {
        padding-left: 30px;
        padding-right: 30px;
    }
}

@media (max-width: 320px) {
    .footer__wrapper {
        padding: 79px 10px 45px;
    }

    .footer__title {
        padding-bottom: 21px;
        margin-bottom: 30px;
        font-size: 12px;
        line-height: 15px;
    }

    .footer__links {
        grid-area: links;
        flex-direction: column;
        align-items: center;
        gap: 12px;
    }

    .footer__date {
        grid-area: date;
        color: #A0A0A0;
        font-size: 12px;
        line-height: 15px;
    }

    .footer__link {
        font-size: 12px;
        line-height: 15px;
    }

    .footer__info-wrapper {
        display: grid;
        grid-template-columns: 1fr;
        justify-items: center;
        grid-template-areas: 'links' 'date';
        gap: 30px;
    }
}